@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;600&display=swap');

body {
  height: 100%;
  margin-right: 20vw;
  background: linear-gradient(90deg, #e6e6e7 80%, #ffffff 20%);
}
h1 {
  font-family: 'Roboto Mono', monospace;
}
h2 {
  font-family: 'Roboto Mono', monospace;
  font-weight: 600;
}
h4 {
  font-weight: 400;
}
.logo-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 48px;
  background: #ff6602;
}
.logo-header img {
  width: 80px;
  height: auto;
}
.page-content {
  font-family: 'Roboto Mono', monospace;
} 
.sideNav {
  border-left: 1px solid black;
  height: 100%; 
  width: 20vw;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  overflow-x: hidden;
  color: black;
}
.search-field {
  width: 50vw;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 3px;
  padding: 8px 20px;
  border: 1px solid black;
  background-color: white;

}
.search-input[type=text]{
  -webkit-appearance:none;
  outline:none;
  border:none;
  width: 50vw;
  font-family: 'Roboto Mono', monospace;
  font-size: 16px;

}
.search-channel {
  color: #ff6602;
  line-height: 1.6;
  text-decoration: none;
}
.navLinks {
  margin-top: 50px;
  padding: 6px 8px 6px 16px;
  text-decoration: none;
  font-family: 'Roboto Mono', monospace;
  font-weight: 600;
  font-size: 16px;
  color: black;
  display: block;
  line-height: 2.8;
}
.navLinks :visited {
  color: black;
}
.popup-box {
  position: fixed;
  background: #00000050;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}
.popup-container {
  position: relative;
  width: 40vw;
  margin: 0 auto;
  height: auto;
  max-height: 70vh;
  margin-top: calc(100vh - 85vh - 20px);
  background: #fff;
  border-radius: 4px;
  padding: 50px;
  border: 1px solid #999;
  overflow: auto;
  box-shadow: 5px 10px #ff6602;
}
.signup-login-btn {
  margin-left:20px;
  padding: 2px 12px;
  background-color: white;
  border: 1px solid #ff6602;
  border-radius: 2px;
  font-family: 'Roboto Mono', monospace;
}
.signup-login-input {
  height: 18px;
}
.close-icon {
  cursor: pointer;
  display: flex;
  justify-content: end;
  align-items: flex-end;
  font-size: 25px;
}
.post-wrap {
  width: 50vw;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 4px;
  padding: 10px 20px;
  border: 1px solid black;
  background-color: white;
  box-shadow: 5px 10px #ff6602;
}
.post-info {
  border-radius: 4px;
  border: 1px solid black;
  background-color: white;
  padding: 10px 30px;
  width: 40vw;
}
.post-top {
  padding-left: 30px;
  display: flex;
  align-items:baseline;
}
.create-post-link {
  color:#ff6602;
  text-decoration: none;
}
.post-comment-creator {
  font-size: 10px;
  margin: 0 12px;
}
.trash-img {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 10px;
}
.comment-icon-container {
  display: flex;
  justify-content:center;
  margin-top: 20px;
}
.comment-info {
  background-color: #e6e6e7;
  border-radius: 4px;
  width: 40vw;
  padding: 10px 30px;
}
.comment-input {
  width: 40vw;
  height: 5vh;
  display: block;
}
.vote-container {
  display: flex;
  justify-content:center;
  align-items: center;
}
.icons {
  margin-right: 20px;
}
.channel-heading {
  position: relative;
  width: 40vw;
  margin: 0 auto;
  padding: 50px;
  overflow: auto;
}